import React, { useState, useContext, useEffect, useRef } from "react";
import { withRouter } from "react-router-dom";
import CardSlider from "components/organism/Slider/CardSlider";
import {
  liveCardData,
  scheduleCardData,
  clipCardData,
  videoCardData,
} from "components/pages/main/data";
import { Link, useLocation } from "react-router-dom/cjs/react-router-dom";
import ScheduleCard from "components/molecule/Card/ScheduleCard";
import ClipCard from "components/molecule/Card/ClipCard";
import VideoCard from "components/molecule/Card/VideoCard";
import BubbleTab from "components/atom/BubbleTab";
import useMediaQueryHook from "hook/useMediaQuery";
import temaMainImg from "assets/images/example/team_image.png";
import teamImg from "assets/images/card/team_2.jpg";
import human from "assets/images/icon/icon_human.svg";
import video from "assets/images/icon/icon_ocarmer.svg";
import share from "assets/images/icon/icon_share.svg";
import hums from "assets/images/icon/Icon_bjoin.svg";
import time from "assets/images/icon/Icon_time.svg";
import zzim from "assets/images/icon/icon_zzim.svg";
import heartstroke from "assets/images/icon/Icon_heartstroke.svg";

import NotiSlider from "components/organism/Slider/NotiSlider";
import Tabs from "components/molecule/tabs/Tabs";
import ImageSlider from "components/organism/Slider/ImageSlider";
import useGetPageTitle from "hook/useGetPageTitle";
import AppHeader from "components/organism/header/AppHeader";
import BtnLayout from "components/molecule/layout/BtnLayout";
import CardList from "components/molecule/list/CardList";

const TeamHome = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const slice = isMobile ? 1 : isTablet ? 2 : 3;

  const location = useLocation();
  const { fixed } = location.fixed || {}; //       

  console.log("fixed", fixed); //           fixed    (new :    / affiliation :  )

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); //   
  const [heart, setHeart] = useState(false); //  
  const [state, setState] = useState("manager"); // user state (join / wait / manager / teammate)
  const [status, setStatus] = useState(false); //  true   .
  const [isFixed, setIsFixed] = useState(false); //  

  const stateImg = state === "join" ? hums : state === "wait" && time;
  const stateText =
    state === "join"
      ? ""
      : state === "wait"
      ? ""
      : state === "manager"
      ? ""
      : state === "teammate"
      ? ""
      : "";
  const handleExpand = () => {
    const section = document.getElementById("video_expanded");

    if (section) {
      const sectionTop = section.getBoundingClientRect().top + window.scrollY;
      window.scrollTo({
        top: sectionTop,
        behavior: "smooth",
      });
    }
    setIsFixed(true);
    setIsExpanded(true);
  };

  // const handleScroll = () => {
  //   const currentScrollY = window.scrollY;
  //   setScrollY(currentScrollY);

  //   if (currentScrollY > prevScrollY && currentScrollY > 100) {
  //     setIsExpanded(false);
  //   } else if (currentScrollY < prevScrollY) {
  //     setIsExpanded(true);
  //   }

  //   setPrevScrollY(currentScrollY);
  // };

  // useEffect(() => {
  //   window.addEventListener("scroll", handleScroll);
  //   return () => {
  //     window.removeEventListener("scroll", handleScroll);
  //   };
  // }, [prevScrollY]);

  const handleHeart = () => {
    setHeart(!heart);
  };

  const handleState = () => {
    setState("wait");
  };

  const videoCard = {
    // totalCnt: "6",
    list: videoCardData,
    cardType: "video",
  };

  const clipCard = {
    totalCnt: "6",
    list: clipCardData,
    cardType: "clip",
  };

  const teamData = {
    name: "",
    member: 30,
    video: 182,
  };

  const notiData = [
    {
      date: "8.26",
      noti: "   ",
      new: true,
    },
    {
      date: "8.26",
      noti: "   ",
      new: false,
    },
    {
      date: "8.26",
      noti: "   ",
      new: true,
    },
  ];

  const btnData = [
    {
      className: "w100 full",
      text: "",
      size: "btn-lg",
      color: "btn-red",
    },
  ];

  const btnData2 = [
    {
      className: "w100",
      text: "",
      size: "btn-lg",
      color: "btn-red",
    },
    {
      className: "w100",
      text: "",
      size: "btn-lg",
      color: "btn-primary",
    },
  ];

  // selectSet setting
  const selectSetComponentRef = useRef({});
  const selectActiveRef = useRef({});
  const selectChangeHandler1 = (e) => {
    //BS cloas
    selectSetComponentRef.current.handleBsClose();
    //change label
    let label = e.target.dataset.label;
    let index = e.target.dataset.index;
    let newBubbleTabData = [...selectClipOptionData];
    newBubbleTabData[index].label = label;
    setSelectClipOptionData(newBubbleTabData);
    // tab active
    selectActiveRef.current.handleTabClick({label:label})
  };
  const [selectClipOptionData, setSelectClipOptionData] = useState([
    {
      select: true,
      selectedTab: "",
      onChange:selectChangeHandler1,
      label: "",
      options: [
        {
          text: " ",
          type:"radio",
        },
        {
          text: " ",
          type:"radio",
        },
        {
          text: " ",
          type:"radio",
        },
      ],
    },
    {
      select: true,
      selectedTab: "",
      onChange:selectChangeHandler1,
      label: "",
      options: [
        {
          text: "   ",
          type:"radio",
        },
        {
          text: "     ",
          type:"radio",
        },
        {
          text: "  ",
          type:"radio",
        },
      ],
    },
    {
      selectedTab: "",
      onClick:"",
      label: "",
    },
  ]);

  const selectChangeHandler2 = (e) => {
    //BS cloas
    selectSetComponentRef.current.handleBsClose();
    //change label
    let label = e.target.dataset.label;
    let index = e.target.dataset.index;
    let newBubbleTabData = [...selectOptionData];
    newBubbleTabData[index].label = label;
    setSelectOptionData(newBubbleTabData);
    // tab active
    selectActiveRef.current.handleTabClick({label:label})
  };
  const [selectOptionData, setSelectOptionData] = useState([
    {
      select: true,
      selectedTab: "",
      onChange:selectChangeHandler2,
      label: "",
      options: [
        {
          text: " ",
          type:"radio",
        },
        {
          text: " ",
          type:"radio",
        },
        {
          text: " ",
          type:"radio",
        },
      ],
    },
    {
      select: true,
      selectedTab: "",
      onChange:selectChangeHandler2,
      label: "",
      options: [
        {
          text: "   ",
          type:"radio",
        },
        {
          text: "     ",
          type:"radio",
        },
        {
          text: "  ",
          type:"radio",
        },
      ],
    },
    {
      selectedTab: "",
      onClick:"",
      label: "",
    },
  ]);

  const tabData = [
    {
      label: "",
      content: (
        <>
          {!isMobile && (
            <section className="sec live_section main">
              <div className="m-main">
                <CardSlider data={liveCardData} />
              </div>
            </section>
          )}
          {/* <section
            id="schedule_section"
            className="sec schedule_section main team"
          >
            <div id="m-main" className="m-main">
              <BubbleTab tabs={selectOptionData} />
              {!isMobile && (
                <>
                  <div className="schedule_label">
                    <div> </div>
                  </div>
                  <div className="card_slider">
                    {scheduleCardData.slice(0, slice).map((item, index) => (
                      <ScheduleCard
                        key={index}
                        progressType={item.progressType}
                        img={item.img}
                        startDate={item.startDate}
                        endDate={item.endDate}
                      />
                    ))}
                  </div>
                </>
              )}
            </div>
          </section> */}
          <section className="sec video_section main team">
            <div className="m-main">
              <div id="video_expanded">
                <BubbleTab
                  tabs={selectOptionData}
                  selectSetRef={selectSetComponentRef}
                  ref={selectActiveRef}
                />
              </div>
              {!isMobile && (
                <div className="video_label">
                  <div> </div>
                </div>
              )}
              {/* <div id="video_expanded" className="card_slider">
                {isMobile
                  ? videoCardData.map((item, index) => (
                      <VideoCard
                        key={index}
                        title={item.title}
                        team1={item.team1}
                        team2={item.team2}
                        score={item.score}
                        stadium={item.stadium}
                        date={item.date}
                        img={item.img}
                      />
                    ))
                  : videoCardData
                      .slice(0, 6)
                      .map((item, index) => (
                        <VideoCard
                          key={index}
                          title={item.title}
                          team1={item.team1}
                          team2={item.team2}
                          score={item.score}
                          stadium={item.stadium}
                          date={item.date}
                          img={item.img}
                        />
                      ))}
              </div> */}

              <CardList data={videoCard} />
            </div>
          </section>
        </>
      ),
    },
    {
      label: "",
      content: (
        <>
          {!isMobile && (
            <section className="sec live_section main">
              <div className="m-main">
                <CardSlider data={liveCardData} />
              </div>
            </section>
          )}
          <section className="sec clip_section main team">
            <div className="m-main">
              <div id="video_expanded">
                <BubbleTab
                  tabs={selectClipOptionData}
                  selectSetRef={selectSetComponentRef}
                  ref={selectActiveRef}
                />
              </div>
              {!isMobile && (
                <div className="clip_label">
                  <div> </div>
                </div>
              )}
              <CardList data={clipCard} />

              {/* <div className="card_slider">
                {isMobile
                  ? clipCardData.map((item, index) => (
                      <ClipCard
                        key={index}
                        title={item.title}
                        team={item.team}
                        content={item.content}
                        stadium={item.stadium}
                        img={item.img}
                      />
                    ))
                  : clipCardData
                      .slice(0, 6)
                      .map((item, index) => (
                        <ClipCard
                          key={index}
                          title={item.title}
                          team={item.team}
                          content={item.content}
                          stadium={item.stadium}
                          img={item.img}
                        />
                      ))}
              </div> */}
            </div>
          </section>
        </>
      ),
    },
  ];

  return (
    <>
      {isMobile && (
        <AppHeader
          className={"content"}
          pageTitle={teamData.name}
          scrolled={true}
        />
      )}
      {/* {isExpanded && isMobile && <AppHeader className={"content"} />} */}
      <div className="subHome_wrapper">
        <section className="subHome_section team">
          <ImageSlider
            images={[temaMainImg, temaMainImg, temaMainImg]}
            overlay={true}
            status={status}
            slideCounter={true}
          />

          <div className={`m-drag_wrapper ${isExpanded ? "active" : ""}`}>
            <div className={`${!isMobile && "m-main"}`}>
              <div className="team_info_wrapper">
                <div className="team_info flexCC">
                  <img src={teamImg} />
                  <div className="team_name">
                    <div>{teamData.name}</div>
                    <Link
                      to={{
                        pathname: "/H-TEAM/04010000",
                        state: {
                          label: " ",
                        },
                      }}
                    >
                      {"   >"}
                    </Link>
                  </div>
                </div>
                <div className="info_container">
                  <div className="info_item">
                    <div className="info_item_num flexCC">
                      <img src={human} />
                      <span>{teamData.member}</span>
                    </div>
                    <Link
                      to={{
                        pathname: "/H-TEAM/05000000",
                        state: {
                          label: "",
                        },
                      }}
                    >
                      {" >"}
                    </Link>
                  </div>
                  <div className="info_item">
                    <div className="info_item_num flexCC">
                      <img src={video} />
                      <span>{teamData.video}</span>
                    </div>
                    <div onClick={handleExpand} style={{ cursor: "pointer" }}>
                      {" >"}
                    </div>
                  </div>
                  <div className="info_item">
                    <img src={share} />
                    
                  </div>
                  <div className="info_item" onClick={handleHeart}>
                    <img src={!heart ? zzim : heartstroke} />
                  </div>
                  <div className="info_item" onClick={handleState}>
                    <div className="info_item_button">
                      {(state === "join" || state === "wait") && (
                        <div>
                          <img src={stateImg} />
                        </div>
                      )}
                      <Link
                        to={
                          state === "manager"
                            ? "/H-TEAM/07030000"
                            : state === "teammate"
                            ? "/H-TEAM/07010000"
                            : "/H-TEAM/06000000"
                        }
                      >
                        {stateText + ">"}
                      </Link>
                    </div>
                  </div>
                </div>
                {notiData && <NotiSlider data={notiData} />}
                {isMobile && (
                  <section className="sec live_section main team">
                    <div className="m-main">
                      <CardSlider data={liveCardData} />
                    </div>
                  </section>
                )}
              </div>
            </div>
            <Tabs tabs={tabData} sticky={true} />
          </div>
        </section>
      </div>
      {fixed && (
        <div className="fix_btn_wrapper">
          <div className={`fix_btn ${isMobile && "mobile"}`}>
            {fixed === "new" ? (
              <BtnLayout attr={btnData2} clasName="full" />
            ) : (
              <BtnLayout
                attr={[
                  {
                    className: "w100 affiliation",
                    text: "",
                    size: "btn-lg",
                    color: "btn-red",
                  },
                ]}
                className="full"
              />
            )}
          </div>
        </div>
      )}
    </>
  );
};

export default withRouter(TeamHome);
