import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import map from "assets/images/example/exMap.png";
import copy from "assets/images/icon/ico_copy.svg";
import phone from "assets/images/icon/ico_phone.svg";

import AppHeader from "components/organism/header/AppHeader";
import Button from "components/atom/button/Button";
import FormItem from "components/molecule/form/FormItem";
import handleChange from "handler/handleChange";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import BtnLayout from "components/molecule/layout/BtnLayout";
import Map from "components/molecule/Map/Map";

const Hteam04010000 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); //   
  const [state, setState] = useState("manager"); // user state
  const [edit, setEdit] = useState(false); //  ui  state
  const [isOpen, setIsOpen] = useState(false); //   
  const maxLength = 65;

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  console.log("isExpanded", isExpanded);

  const handleToggle = () => {
    setIsOpen(!isOpen); //  / 
  };

  const handleEdit = () => {
    setEdit(!edit); //  ui 
  };

  const [teamData, setTeamData] = useState({
    name: "",
    member: 30,
    video: 182,
    phone: "02-262-7899",
    officeAddress: [
      "   32 66  201",
      "   32 66  202",
    ],
    stadiumAddress: "   223",
    website: "https://aisportstv.co.kr/",
    association:
      " U-13      - U13() ,  , ,  ",
    foundation: "2011 ",
    manager: " ",
    trainingTime: "- ,,, 16:00~18:30 <br/> -  14:30~17:00 ",
    trainingSite: " /  ",
  });

  const infoForm = [
    {
      label: "",
      id: "phone",
      name: "phone",
      type: "text",
      value: teamData.phone,
      onChange: (e) => handleChange(e, teamData, setTeamData),
    },

    {
      label: " ",
      id: "officeAddress",
      name: "officeAddress",
      type: "text",
      value: teamData.officeAddress,
      onChange: (e) => handleChange(e, teamData, setTeamData),
      link: {
        pathname: "/H-TEAM/04020201",
        label: teamData.name,
        name: " ",
      },
    },
    {
      label: " ",
      id: "stadiumAddress",
      name: "stadiumAddress",
      type: "text",
      value: teamData.stadiumAddress,
      onChange: (e) => handleChange(e, teamData, setTeamData),
      link: {
        pathname: "/H-TEAM/04020201",
        label: teamData.name,
        name: " ",
      },
    },
    {
      label: "",
      id: "website",
      name: "website",
      type: "text",
      value: teamData.website,
      onChange: (e) => handleChange(e, teamData, setTeamData),
    },
    {
      label: " ",
      id: "association",
      name: "association",
      type: "textarea",
      value: teamData.association,
      onChange: (e) => handleChange(e, teamData, setTeamData),
    },
  ];

  return (
    <>
      {/* {isMobile && (
        <AppHeader className={"content"} pageTitle={teamData.name} />
      )} */}
      <div className="subHome_wrapper">
        {!isMobile && !edit ? (
          <div className="teamInfo_Page flexCC"></div>
        ) : !isMobile && edit ? (
          <div className="teamInfo_Page flexCC">{teamData.name}</div>
        ) : (
          ""
        )}

        <div className={`${!isMobile && "m-main"}`}>
          {!edit && (
            <section className="subHome_section teamInfo_map flexCC">
              <div className="map_wrapper flexCC">
                {/* <img src={map} /> */}
                <Map />
              </div>
            </section>
          )}
        </div>
        <section className="subHome_section teamInfo flexCC">
          {!edit && <div className="team_title">{teamData.name}</div>}
          <div className={`team_inforamtion ${edit && "edit"}`}>
            {/* edit false     */}
            {!edit ? (
              <>
                <div className="info-row">
                  <span className="label"></span>
                  <a href="" className="info">
                    {teamData.phone}
                    <img src={phone} />
                  </a>
                </div>
                <div className="info-row">
                  <span className="label"> </span>
                  <span className="info-btn">
                    {teamData.officeAddress}{" "}
                    <Button
                      attr={{
                        className: `ico-copy`,
                        iconOnly: true,
                      }}
                    />
                  </span>
                </div>
                <div className="info-row">
                  <span className="label"> </span>
                  <span className="info-btn">
                    {teamData.stadiumAddress}{" "}
                    <Button
                      attr={{
                        className: `ico-copy`,
                        iconOnly: true,
                      }}
                    />
                  </span>
                </div>
                <div className="info-row">
                  <span className="label"></span>
                  <a href={teamData.website} className="info">
                    {teamData.website}
                  </a>
                </div>
                <div className="info-row">
                  <span className="label-toggle">
                     {" "}
                    <span className="toggle-icon" onClick={handleToggle}>
                      {isOpen || teamData.association.length >= maxLength ? (
                        <Button
                          attr={{
                            className: isOpen ? `ico-dn` : `ico-up`,
                            iconOnly: true,
                          }}
                        />
                      ) : (
                        ""
                      )}
                    </span>
                  </span>
                  <div className="info" style={{ cursor: "pointer" }}>
                    {isOpen || teamData.association.length <= maxLength ? (
                      teamData.association
                    ) : (
                      <>{teamData.association.substring(0, maxLength)}...</>
                    )}
                  </div>
                </div>
              </>
            ) : (
              // edit true  form 
              <div className={`join_term_wrapper ${isMobile && "mobile"}`}>
                <div className="form-box">
                  <div className="form-container">
                    {infoForm.map((item, index) => (
                      <FormItem key={index} attr={item} />
                    ))}
                    <div className="col-right">
                      <span>0</span>/30
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        </section>

        <section className="subHome_section etcInfo">
          <div className="etcInfo_wrapper">
            <div className="etcInfo_container">
              <div className="ico logo">
                 {" "}
                <div className={edit && "more"}>
                  {edit && <Link to={`/H-TEAM/04020200`}> </Link>}
                </div>
              </div>
              <div className="etcInfo_content">
                <div>
                  <span className="label"></span>
                  <span className="info">{teamData.foundation} </span>
                </div>
                <div>
                  <span className="label"></span>
                  <span className="info">{teamData.manager} </span>
                </div>
              </div>
            </div>
            <div className="etcInfo_container">
              <div className="ico logo">
                 {" "}
                <div className={edit && "more"}>
                  {edit && <Link to={`/content`}> </Link>}
                </div>
              </div>
              <div className="etcInfo_content">
                <div>
                  <span className="label"></span>
                  <span
                    className="info"
                    dangerouslySetInnerHTML={{ __html: teamData.trainingTime }}
                  />
                </div>
                <div>
                  <span className="label"></span>
                  <span className="info">{teamData.trainingSite} </span>
                </div>
              </div>
            </div>
          </div>
          {state === "manager" && !edit && (
            <div className="fixed-button">
              <Button
                attr={{
                  className: `ico-edit`,
                  iconOnly: true,
                  onClick: handleEdit,
                }}
              />
            </div>
          )}
        </section>
      </div>

      {edit && (
        <div className={`${isMobile ? "fixed-save-button" : "save-button"}`}>
          <BtnLayout
            attr={[
              {
                className: "w100",
                text: "",
                size: "btn-lg",
                color: "btn-primary",
              },
            ]}
            className="full"
          />
        </div>
      )}
    </>
  );
};

export default withRouter(Hteam04010000);
