/**
 *
 * @param {*} props label : 
 * @param {*} props id : id 
 * @param {*} props name : input 
 * @param {*} props defaultValue : *
 * @param {*} props status :   (is-valid / is-invalid)
 * @param {*} props onChange : input   
 * @param {*} props checked : 
 *
 */

// Checkbox  , id      GroupId   ,
// name        .

const Checkbox = ({ attr }) => {
  const {
    id,
    status,
    name,
    className,
    onChange,
    checked,
    label,
    dataGroup,
    disabled,
    size,
    shape,
    require,
  } = attr;

  const checkboxClass = `checkbox checkbox-css ${
    status ? status : "checkbox-inline"
  } ${className} ${size} ${shape}`;

  return (
    <div key={id} className={checkboxClass}>
      <input
        type="checkbox"
        id={id}
        name={name || id}
        checked={checked}
        disabled={disabled}
        onChange={onChange}
        data-group={dataGroup}
      />
      <label htmlFor={id}>
        {require === true ? "()" : require === false ? "()" : ""}{" "}
        {label}
      </label>
    </div>
  );
};

export default Checkbox;
