/**
 *
 * @param {*} props type : badge  [live / clip / game]
 * @param {*} props style : 
 * @param {*} props text : badge  
 * @param {*} props img : img
 * @param {*} props live : text  live 
 */

const Badge = ({ attr = {} }) => {
  const { type, style, text, live, img } = attr;

  const typeText =
    type === "live"
      ? ""
      : type === "clip"
      ? ""
      : type === "game"
      ? ""
      : type === "yet"
        ? ""
      : "";

  return (
    <div className={`badge ${type}`} style={style}>
      {img && <img src={img} alt="badge" />}
      {text ? text : typeText}
      {live && <span className="badge_live" />}
    </div>
  );
};

export default Badge;
