import React, { useEffect, useRef } from "react";
import Slider from "react-slick";

import "./carousel.scss";
import KvCard from "components/molecule/Banner/KvBanner";

/* react-slick  */

// rows: 1,                        
// dots: false,                      (true or false)  false
// appendDots: $('selector'),    
// dotsClass: 'custom-dots',      
// infinite: true,             (true or false)  true
// slidesToShow: 4,                
// slidesToScroll: 4,              
// slidesPerRow: 1,               ( ,   ... )
// autoplay: true,               (true or false)  false
// autoplaySpeed: 2000,           (1000ms = 1) ,    
// variableWidth: true,         width  ?(true or false)  false
// draggable: false,              (true or false)  true
// arrows: true,                   (true or false)  true
// pauseOnFocus: true,               true
// pauseOnHover: true,               true
// pauseOnDotsHover: true,           false
// vertical: true,               (true or false)  false
// verticalSwiping: true,         (true or false)  false
// accessibility: true,         (true or false)  false
// appendArrows: $('#arrows'),   
// prevArrow: $('#prevArrow'),   
// nextArrow: $('#nextArrow'),   
// initialSlide: 1,                 0
// centerMode: true,                false
// centerPadding: '70px',           
// fade: true,                      false
// speed: 2000,                  (  )(1000ms = 1) ,    
// waitForAnimate: true,            true

export default function Carousel({data}) {
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    appendDots: (dots) => (
      <div
        className="slideer-wrapper"
        style={{
          position: "absolute",
          top: "80%",
          left: "50%",
          transform: "translateX(-50%)",
        }}
      >
        <ul style={{ margin: "0" }}>{dots}</ul>
      </div>
    ),
    responsive: [
      {
        breakpoint: 360,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          adaptiveHeight: true,
          appendDots: (dots) => (
            <div
              className="slideer-wrapper"
              style={{
                position: "absolute",
                right: "5%",
              }}
            >
              <ul style={{ margin: "0" }}>{dots}</ul>
            </div>
          ),
        },
      },
      {
        breakpoint: 860, // 860px 
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 1200, // 1200px 
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <div className="carousel_wrapper">
      <Slider {...settings}>
        {data.map((item, index) => (
          <div className="kv-slider" key={index}>
            <KvCard
              img={item.img}
              bg={item.bg}
              rightImg={item.rightImg}
              title={item.title}
              dec={item.dec}
            />
          </div>
        ))}
      </Slider>
    </div>
  );
}
