import React, {useState} from 'react';
import { withRouter } from "react-router-dom";
import { toast } from "react-toastify";
import Confirm from "components/molecule/alert/Confirm";
import Button from "components/atom/button/Button";

const ConfirmData = [
  {
    popBtn : " ",
    smallTit: "     .",
    balls: [
      {
        dt :" ",
        ball :"10,000",
      },
      {
        dt :" ",
        ball :"1000",
      }
    ],
    button: {
      text: " ",
      onClick: "",
    },
  },
  {
    popBtn : "",
    smallTit: "     1000 .\n    .",
    balls: [
      {
        dt :" ",
        ball :"10,000",
      },
      {
        dt :" ",
        ball :"1000",
      }
    ],
    button: {
      text: " ",
      onClick: "",
    },
  },
  {
    popBtn : "   1",
    dec: "   1\n .\n1    .",
    button: {
      text : "",
      onClick : "",
    },
  },
  {
    popBtn : "  ",
    title: "  ",
    balls: [
      {
        dt :" ",
        ball :"10,000",
      },
      {
        dt :"",
        ball :"00 00",
      },
      {
        dt :" ",
        ball :"500(3 )",
      }
    ],
    button: {
      text: ' ',
      onClick: "",
    },
  },
  {
    popBtn : "  ",
    title: "  ",
    balls: [
      {
        dt :" ",
        ball :"10,000",
      },
      {
        dt :"",
        ball :"00 00",
      },
      {
        dt :" ",
        ball :"500(3 )",
      }
    ],
    button: {
      text: '  ',
      onClick: "",
    },
  },
  {
    popBtn : " ",
    title: "  ?",
    dec : "*   .",
    button: {
      text: '',
      onClick: "",
    },
  },
  {
    popBtn : " ",
    title: " ",
    chkList: [
      {
        type:"radio",
        name :"r1",
        label : "",
        checked : true,
      },
      {
        type:"radio",
        name :"r1",
        label : "",
      },
      {
        type:"radio",
        name :"r1",
        label : "",
      },
    ],
    button: {
      text: '',
      onClick: "",
    },
  },
  {
    popBtn: "",
    title: " \n?",
    button: {
      text: "",
      onClick: "",
    },
  }
]

const Hvod_pop_2 = (props) => {
  const [confirmOpen, setConfirmOpen] = useState(false);

  const openConfirmHandler = (id) => {
    setConfirmOpen((el) => (el.confirmOpen !== id ? id : ""));
    if (confirmOpen === id) {
      setConfirmOpen(false);
    }
  };

  const handleShowToast = (msg) => {
    return () => toast(msg, {
      position: "top-center",
    });
  };

  return (
    <>
      <section>
        <div className="m-main">
          <ul className="flex">

            {ConfirmData.map((item, index) => (
              <li key={index} style={{margin:"2px"}}>
                <button key={index} className="btn btn-xxs" onClick={ () => {openConfirmHandler(index)} }>{item.popBtn}</button>
                {confirmOpen === index ? <Confirm data={item} onClose={openConfirmHandler} id={index} /> : ""}
              </li>
            ))}
            <li style={{margin:"2px"}}>
              <Button
                attr={{
                  size:"btn-xxs",
                  text: "  toast",
                  onClick:handleShowToast("  ."),
                }}
              />
            </li>
            <li style={{margin:"2px"}}>
              <Button
                attr={{
                  size:"btn-xxs",
                  text: " toast",
                  onClick:handleShowToast("."),
                }}
              />
            </li>
          </ul>
        </div>
      </section>
    </>
  );
};

export default Hvod_pop_2;
