import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";
import useMediaQueryHook from "hook/useMediaQuery";
import Checkbox from "components/atom/Checkbox";
import handleChange from "handler/handleChange";
import FormItem from "components/molecule/form/FormItem";
import BtnLayout from "components/molecule/layout/BtnLayout";

const Hlog01010201 = (props) => {
  const { currentMediaQuery, isMobile } = useMediaQueryHook();

  const [formData, setFormData] = useState({
    name: "",
    birthdate: "",
    phone: "",
  });

  const isFormValid = () => {
    return (
      formData.name !== "" && formData.birthdate !== "" && formData.phone !== ""
    );
  };

  const joinForm = [
    {
      label: "",
      labelClassName: "",
      formClassName: "",
      formBtns: "",
      copyToClipboard: "",
      id: "name",
      name: "name",
      type: "text",
      value: formData.name,
      status: "",
      onChange: (e) => handleChange(e, formData, setFormData),
      readOnly: false,
      onKeyPress: "",
      placeholder: " .",
      autocomplete: "",
      inValidMsg: "",
    },
    {
      label: "",
      labelClassName: "",
      formClassName: "",
      formBtns: "",
      copyToClipboard: "",
      id: "birthdate",
      name: "birthdate",
      type: "text",
      value: formData.birthdate,
      status: "",
      onChange: (e) => handleChange(e, formData, setFormData),
      readOnly: false,
      onKeyPress: "",
      placeholder: " . (EX. 19900101)",
      autocomplete: "",
      inValidMsg: "",
    },
    {
      label: " ",
      labelClassName: "",
      formClassName: "",
      formBtns: "",
      copyToClipboard: "",
      id: "phone",
      name: "phone",
      type: "text",
      value: formData.phone,
      status: "",
      onChange: (e) => handleChange(e, formData, setFormData),
      readOnly: false,
      onKeyPress: "",
      placeholder: "010-1234-1234",
      autocomplete: "",
      inValidMsg: "",
    },
  ];

  return (
    <>
      <div className="page-wrapper joinPage bg-gray">
        <section className="join_wrapper sec-height _fix _between">
          <div className="join_container">
            <div className="col-top">
              <div className="m-main">
                <div className="join_info">
                   14  
                  <br />  .
                </div>

                <div className="form-box">
                  <div className="form-container _wrap">
                    {joinForm.map((item, index) => (
                      <FormItem key={index} attr={item} />
                    ))}
                  </div>
                </div>
              </div>
            </div>

            <div className="btt-btn">
              <div className="m-main">
                <BtnLayout
                  attr={[
                    {
                      className : 'w100',
                      text: '',
                      disabled: !isFormValid(),
                      size: "btn-xxl",
                      color : 'btn-primary',
                      onClick: "",
                      radius:false,
                    },
                  ]}
                  className="full"
                />
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
};

export default withRouter(Hlog01010201);
