import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom";
import useMediaQueryHook from "hook/useMediaQuery";
import Checkbox from "components/molecule/form/Checkbox";
import handleCheck from "handler/handleCheck";
import Confirm from "components/molecule/alert/Confirm";
import handleLocation from "handler/handleLocation";
import BtnLayout from "components/molecule/layout/BtnLayout";

const Hlog01010000 = (props) => {
  const { currentMediaQuery, isMobile } = useMediaQueryHook();
  const [confirmOpen, setConfirmOpen] = useState(false);

  const openConfirmHandler = (id) => {
    setConfirmOpen(!confirmOpen);
  };

  const [data, setData] = useState({
    terms: {
      all: false,
      ageChecked: false,
      termsChecked: false,
      privacyChecked: false,
      thirdPartyChecked: false,
      marketingChecked: false,
    },
  });

  const ConfirmData = {
    icon: true,
    title: " 14 ?",
    dec: " 14    .",
    button: [
      {
        text: "",
        color: "btn-line-white",
        onClick: "",
      },
      {
        text: "",
        onClick: handleLocation("/H-LOG/01010201"),
      },
    ],
  };

  const isAllRequiredChecked =
    data.terms.termsChecked &&
    data.terms.thirdPartyChecked &&
    data.terms.privacyChecked;
  return (
    <>
      <div className="page-wrapper joinPage bg-gray">
        <section className="join_wrapper sec-height _fix">
          <div className="join_container t2">
            <div className="col-top">
              <div className="m-main">
                <div className="join_info">
                  
                <br /> .
              </div>
              </div>
            </div>

            <div className={`join_term_wrapper ${isMobile && "mobile"}`}>
              <div className="m-main">
                <Checkbox
                  attr={{
                    id: "checkbox-all",
                    label: "",
                    checked: data.terms.all,
                    onChange: (e) => handleCheck(e, data, setData),
                    size: "big",
                    shape: "round",
                    name: "all",
                    className: "main-checkbox",
                    dataGroup: "terms",
                  }}
                />

                <Checkbox
                  attr={{
                    id: "checkbox-age",
                    label: " 14 .",
                    checked: data.terms.ageChecked,
                    onChange: (e) => handleCheck(e, data, setData),
                    size: "small",
                    shape: "square",
                    name: "ageChecked",
                    dataGroup: "terms",
                  }}
                />
                <Checkbox
                  attr={{
                    id: "checkbox-terms",
                    label: (
                      <Link
                        to={{
                          pathname: "/H-TERM/00000001",
                          state: { label: " " },
                        }}
                      >
                          &gt;{" "}
                      </Link>
                    ),
                    checked: data.terms.termsChecked,
                    onChange: (e) => handleCheck(e, data, setData),
                    size: "small",
                    shape: "square",
                    name: "termsChecked",
                    dataGroup: "terms",
                    require: true,
                  }}
                />
                <Checkbox
                  attr={{
                    id: "checkbox-privacy",
                    label: (
                      <Link
                        to={{
                          pathname: "/H-TERM/00000001",
                          state: { label: " , " },
                        }}
                      >
                         ,  &gt;{" "}
                      </Link>
                    ),
                    checked: data.terms.privacyChecked,
                    onChange: (e) => handleCheck(e, data, setData),
                    size: "small",
                    shape: "square",
                    name: "privacyChecked",
                    dataGroup: "terms",
                    require: true,
                  }}
                />
                <Checkbox
                  attr={{
                    id: "checkbox-third-party",
                    label: (
                      <Link
                        to={{
                          pathname: "/H-TERM/00000001",
                          state: { label: " 3  " },
                        }}
                      >
                         3   &gt;{" "}
                      </Link>
                    ),
                    checked: data.terms.thirdPartyChecked,
                    onChange: (e) => handleCheck(e, data, setData),
                    size: "small",
                    shape: "square",
                    name: "thirdPartyChecked",
                    dataGroup: "terms",
                    require: true,
                  }}
                />
                <Checkbox
                  attr={{
                    id: "checkbox-marketing",
                    label: (
                      <Link
                        to={{
                          pathname: "/H-TERM/00000001",
                          state: { label: "  " },
                        }}
                      >
                           &gt;{" "}
                      </Link>
                    ),
                    checked: data.terms.marketingChecked,
                    onChange: (e) => handleCheck(e, data, setData),
                    name: "marketingChecked",
                    dataGroup: "terms",
                    require: false,
                  }}
                />
              </div>
            </div>


            <div className="btt-btn">
              <div className="m-main">
                <BtnLayout
                  attr={[
                    {
                      className : 'w100',
                      text: '',
                      disabled: !isAllRequiredChecked,
                      size: "btn-xxl",
                      color : 'btn-primary',
                      radius:false,
                      onClick: () => openConfirmHandler(),
                    },
                  ]}
                  className="full"
                />
              </div>
            </div>
          </div>
        </section>
      </div>
      {confirmOpen && (
        <Confirm data={ConfirmData} onClose={openConfirmHandler} />
      )}
    </>
  );
};

export default withRouter(Hlog01010000);
