/**
 *
 * @param {*} props label : 
 * @param {*} props id : id 
 * @param {*} props name : input 
 * @param {*} props defaultValue : *
 * @param {*} props status :   (is-valid / is-invalid)
 * @param {*} props onChange : input   
 * @param {*} props checked : 
 *
 */

import Badge from "components/atom/Badge";
import ball from "assets/images/card/icon_ball.svg";

// Checkbox  , id      GroupId   ,
// name        .

const VideoCard = ({
  title,
  team1,
  team2,
  stadium,
  img,
  score,
  date,
  onClick,
}) => {
  return (
    <div
      className="card_wrapper video"
      style={{ backgroundImage: `url(${img})` }}
      onClick={onClick}
    >
      <div className="card_top video">
        <ul className="header flex">
          <li>
            <Badge attr={{ type: "game" }} />
            <div className="title">{title}</div>
          </li>
          <li>
            <div className="live_icon">
              <img src={ball} alt="Ball Icon" />
            </div>
          </li>
        </ul>
      </div>
      <div className="card_bottom video">
        <div className="card_team">
          <div>
            <img src={team1.teamLogo} />
          </div>
          <div>{team1.teamName}</div>
        </div>
        <div className="score_wrapper">
          <div className="stadium">{stadium}</div>
          <div className="game_score">
            <span
              className={`score ${team1.score > team2.score ? "_win" : ""}`}
            >
              {team1.score}
            </span>
            <span>:</span>
            <span
              className={`score ${team1.score < team2.score ? "_win" : ""}`}
            >
              {team2.score}
            </span>
          </div>
          <div className="game_date">{date}</div>
        </div>
        <div className="card_team">
          <div>
            <img src={team2.teamLogo} />
          </div>
          <div>{team2.teamName}</div>
        </div>
      </div>
    </div>
  );
};

export default VideoCard;
