const Hmain07030000 = (props) => {
  return (
    <>
      <PageHeader onClick="" title="" />
      <div className="page page-wrapper page-shoot">
        <FullHeight attr={{pageHeader:true}}>
          <ColTop>
            <div className="location-container">
              <header className="location-header">
                <strong className="stadium">   A</strong>
                <p className="date">2024 10 24 12:00~14:55</p>
              </header>
              <p className="tip">*      5(55) .</p>
            </div>
            <div className="info-container">
              <div className="label-game">
                <strong className="game baseball"></strong>
              </div>

              <div className="shoot-container">
                <dl className="shoot-dl">
                  <dt><span className="req"></span></dt>
                  <dd>
                    <div className="form-box">
                      <BubbleTab tabs={[{ label: "" }, { label: "" }, { label: "" }]}/>
                    </div>
                  </dd>
                </dl>

                <div className="shoot-dl form-group">
                  <FormSearch attr={{
                    name: "",
                    placeholder: " .",
                    onClick: "",
                  }}
                  />
                </div>

                <dl className="shoot-dl">
                  <dt><span className="req"></span></dt>
                  <dd>
                    {infoCardData.map((item, index) => (
                      <InfoCard
                        type={item.type}
                        team1={item.team1}
                        team2={item.team2}
                      />
                    ))}
                  </dd>
                </dl>
              </div>

              <div className="form-container _wrap">
                { formGroup
                  .map((item, index) => (
                    <FormItem
                      key={index}
                      attr={item}
                    />
                  ))
                }
              </div>
            </div>
          </ColTop>
          <ColBottom>
            <div className="agree-container">
              <BtnLayout
                attr={[
                  {
                    className : 'w100',
                    text: '',
                    color:"btn-gray",
                    size:"btn-lg",
                    onClick:"",
                    alertDatas : {
                      alert:"",
                      dec:" \n?",
                      button:{
                        text:"",
                        onClick:"",
                      },
                    }
                  },
                  {
                    className : 'w100',
                    text: '',
                    color:"btn-primary",
                    size:"btn-lg",
                    onClick:"",
                    alertDatas : {
                      alert:true,
                      dec:" 20 \n .",
                      button:{
                        text:"",
                        onClick:"",
                      },
                    }
                  },
                ]}
                className="half"
              />
            </div>
          </ColBottom>
        </FullHeight>
      </div>
      {popData.map((item, index) => (
        <ButtonAlert attr={item} />
      ))}
    </>
  );
};

export default withRouter(Hmain07030000);
