import React, {useRef, useState} from "react";
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom";
import Button from "components/atom/button/Button";
import BubbleTab from "components/atom/BubbleTab";
import BbsCard from "components/molecule/Card/BbsCard";
import CardList from "components/molecule/list/CardList";

import {
  scheduleCardData,
  liveCardData,
} from "components/pages/main/data";

const scheduleData = [
  {
    date: "9/7()",
    datas : {
      totalCnt : "2",
      list : scheduleCardData,
      cardType : "schedule"
    }
  },
  {
    date: "9/1()",
    datas : {
      totalCnt : "2",
      list : scheduleCardData,
      cardType : "schedule"
    }
  },
]

const liveCard = {
  totalCnt : "2",
  list : liveCardData,
  cardType : "live"
}

const Hmain06000000 = (props) => {

  // selectSet setting
  const selectSetComponentRef = useRef({});
  const selectActiveRef = useRef({});
  const selectChangeHandler = (e) => {
    //BS cloas
    selectSetComponentRef.current.handleBsClose();
    //change label
    let label = e.target.dataset.label;
    let index = e.target.dataset.index;
    let newBubbleTabData = [...bubbleTabData];
    newBubbleTabData[index].label = label;
    setBubbleTabData(newBubbleTabData);
    // tab active
    selectActiveRef.current.handleTabClick({label:label})
  };
  const [bubbleTabData, setBubbleTabData] = useState([
    {
      refesh: true,
      onClick : "",
      cnt : "1",
    },
    {
      select: true,
      selectedTab : "",
      label: "",
      onChange:selectChangeHandler,
      name:"bubble1",
      options: [
        {
          text: "",
          type:"radio",
        },
        {
          text: "1",
          type:"radio",
        },
        {
          text: "2",
          type:"radio",
        },
        {
          text: "3",
          type:"radio",
        },
        {
          text: "4",
          onClick : "",
        },
      ],
    },
    {
      select: true,
      selectedTab : "",
      label: "",
      onChange:selectChangeHandler,
      name:"bubble2",
      options: [
        {
          text: "",
          type:"radio",
        },
        {
          text: "1",
          type:"radio",
        },
        {
          text: "2",
          type:"radio",
        },
        {
          text: "3",
          type:"radio",
        },
        {
          text: "4",
          type:"radio",
        },
      ],
    },
    {
      select: true,
      selectedTab : "",
      label: "",
      onChange:selectChangeHandler,
      name:"bubble3",
      options: [
        {
          text: "",
          type:"radio",
        },
        {
          text: "1",
          type:"radio",
        },
        {
          text: "2",
          type:"radio",
        },
        {
          text: "3",
          type:"radio",
        },
        {
          text: "4",
          type:"radio",
        },
      ],
    },
    {
      select: true,
      selectedTab : "",
      label: "",
      onChange:selectChangeHandler,
      name:"bubble4",
      options: [
        {
          text: "",
          type:"radio",
        },
        {
          text: "  1",
          type:"radio",
        },
        {
          text: "  2",
          type:"radio",
        },
        {
          text: "  3",
          type:"radio",
        },
      ],
    },
    {
      select: true,
      selectedTab : "",
      label: "",
      onChange:selectChangeHandler,
      name:"bubble5",
      options: [
        {
          text: "",
          type:"radio",
        },
        {
          text: "  1",
          type:"radio",
        },
        {
          text: "  2",
          type:"radio",
        },
        {
          text: "  3",
          type:"radio",
        },
      ],
    },
  ])

  return (
    <>
      <div className="page-wrapper top-fix page-playSchedule">
        <div className="top-select">
          <div className="m-main">
            <BubbleTab
              tabs={bubbleTabData}
              selectSetRef={selectSetComponentRef}
              ref={selectActiveRef}
            />
          </div>
        </div>

        <section className="sec sec-video bg-white">
          <div className="m-main">
            <ul className="bbs-list flex">
              <li>
                <BbsCard
                  type="nodata"
                  dec={`/   !\n     .`}
                />
              </li>
              <li>
                <BbsCard
                  type="nodata"
                  dec={`  .`}
                />
              </li>
              <li>
                <BbsCard
                  type="nodata"
                  dec={`    .\n !`}
                />
              </li>
            </ul>
            <header className="mont-container">
              <Button attr={{
                className : `ico-prev`,
                text: '',
                iconOnly: true,
                onClick: "",
              }}
              />
              <strong className="month">2024.09</strong>
              <Button attr={{
                className : `ico-next`,
                text: '',
                iconOnly: true,
                onClick: "",
              }}
              />
            </header>
            <CardList data={liveCard}/>
          </div>
        </section>
      </div>
    </>
  );
};

export default withRouter(Hmain06000000);
