import React, {useRef, useState} from "react";
import useMediaQueryHook from "../../../hook/useMediaQuery";
import Sticky from 'react-sticky-el';
import ClipCard from "components/molecule/Card/ClipCard_v2";
import Button from "components/atom/button/Button";
import BubbleTab from "components/atom/BubbleTab";
import FormItem from "components/molecule/form/FormItem";
import BtnLayout from "components/molecule/layout/BtnLayout";
import SelectSet from "components/molecule/form/SelectSet";

/**
 *
 * @param {*} props tab : clip || tag || analysis
 * @param {*} props type : list || write
 * @param {*} props btn.text : text
 * @param {*} props btn.onClick : onClick
 * @param {*} props btn.color : default btn-primary || btn-black
 * @param {*} props datas : datas
 */

const bubbleTabData = [
  {
    label: "",
    id:"",
    name:"selectedTabName",
    select: true,
    selectedTab: "",
    onClick: "",
    options: [
      {
        text: " ",
        value: "",
        name: "s1",
        rows : {
          type : "btn",
          name:"s1",
          checked:"",
          datas: [
            {
              text: "1",
              value:"1",
              onClick:"",
            },
            {
              text: "2",
              value:"2",
              onClick:"",
            },
            {
              text: "3",
              value:"3",
              onClick:"",
            },
          ]
        }
      },
      {
        text: " ",
        value: "",
        name: "s1",
        rows : {
          type : "radio",
          name:"s2",
          checked:"",
          datas: [
            {
              text: " 1",
              value:" 1",
              checked:"",
            },
            {
              text: " 2",
              value:" 2",
              checked:"",
            },
            {
              text: " 3",
              value:" 3",
              checked:"",
            },
          ]
        }
      },
      {
        text: " ",
        value: "",
        name: "s1",
        rows : {
          type : "radio",
          name:"s3",
          checked:"",
          datas: [
            {
              text: " 1",
              value:" 1",
              checked:"",
            },
            {
              text: " 2",
              value:" 2",
              checked:"",
            },
            {
              text: " 3",
              value:" 3",
              checked:"",
            },
          ]
        }
      },
    ],
    layers: {
      selectType: "btn",
      optionLabel: "",
      tagHide: true,
      optionType: "radio",
    },
  },
];

const TabContainer = ({attr, changeParentsFn}) => {
  const {tabData, onClick } = attr;
  const {tab, datas, tagList } = tabData;
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const SelectSetCompRef = useRef({});

  const [fmVisible, setFmVisible] = useState(false);
  const handleFmOpen = (sid) => {
    setFmVisible(!fmVisible);
    changeParentsFn(sid);
  };

  const handleUpdateOpen = () => {
    SelectSetCompRef.current.handleBsClose();
    setFmVisible(!fmVisible);
  };

  const handleTaglayerOpen = () => {
    console.log("handleTaglayerOpen")
    changeParentsFn("tagLayer");
  };

  const handleFmClose = () => {
    setFmVisible(!fmVisible)
  };

  const handleMove = () => {
    window.location.href="/H-VOD/01000000";
  };

  const clipFmData1 = [
    {
      label: "",
      type: "p",
      value: "*   .",
    },
    {
      label: "",
      type: "text",
      id: "text",
      name: "text",
      value: "",
      required: "",
      placeholder: " .",
      status: "",
      checked: "",
      readOnly: "",
      disabled: "",
      onChange: "",
      onKeyPress: "",
      onKeyUp: "",
      onBlur: "",
      copyToClipboard: "",
      autocomplete: "",
      formBtns: {
        noBtn : "",
        use: "",
        type: "",
        text: "",
        className: "",
        onClick: "",
        disabled: "",
      },
    },
    {
      label: "",
      type: "radio",
      id: "radio",
      name: "",
      value: "",
      required: true,
      status: "",
      shape:"",
      size:"",
      options: [
        {
          text:"",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:" ",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:" ",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
      ],
    },
  ]

  const tagFmData1 = [
    {
      label: "",
      type: "select",
      id: "select",
      name: "",
      value: "",
      required: true,
      placeholder: " .",
      status: "",
      disabled: "",
      onChange: "",
      onKeyPress: "",
      onKeyUp: "",
      onBlur: "",
      options: [
        {
          text: "1",
          value:"",
          status: "",
        },
        {
          text: "2",
          value:"",
          status: "",
        },
        {
          text: "3",
          value:"",
          status: "",
        },
      ],
      layers : {
        selectType: "select",
        optionLabel: "",
        tagHide: "",
        optionType: "radio",
      },
    },
    {
      label: "",
      type: "text",
      id: "text",
      name: "text",
      value: "",
      required: "",
      placeholder: "  .",
      status: "",
      checked: "",
      readOnly: "",
      disabled: "",
      onChange: "",
      onKeyPress: "",
      onKeyUp: "",
      onBlur: "",
      copyToClipboard: "",
      autocomplete: "",
      formBtns: {
        noBtn : "",
        use: "",
        type: "",
        text: "",
        className: "",
        onClick: "",
        disabled: "",
      },
    },
    {
      label: "",
      type: "radio",
      id: "radio",
      name: "",
      value: "",
      required: true,
      status: "",
      shape:"",
      size:"",
      options: [
        {
          text:"",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:" ",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:" ",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
        {
          text:"",
          value : "",
          status : "",
          onChange : "",
          checked: "",
        },
      ],
    },
  ]

  return (
    <>
      <div className={`tab-item ${tab}`}>
        <div className="content-item">
          {/*datas */}
          {tab === "highlight" ? (
            <dl className="nodata">
              <dd>  .</dd>
            </dl>
          ) : tab === "clip" ? (
            !fmVisible && (
              <dl className="nodata">
                <dt> .</dt>
                <dd>      .</dd>
              </dl>
            )
          ) : tab === "tag" ? (
            !fmVisible && (
              <dl className="nodata">
                <dt> .</dt>
                <dd>        .</dd>
              </dl>
            )
          ) : (
            <dl className="nodata">
              <dd>BO  <br /> PDF </dd>
            </dl>
          )}

          {fmVisible && (
            tab === "clip" ? (
              <>
                <div className="form-box">
                  <div className="form-container">
                    { clipFmData1.map((item, index) => (
                      <FormItem
                        key={index}
                        attr={item}
                      />
                    ))
                    }
                  </div>
                </div>

                <div className="btt-btn t1">
                  <BtnLayout
                    attr={[
                      {
                        className : 'w100',
                        text: '',
                        size : 'btn-lg',
                        color : 'btn-gray',
                        onClick:() => handleFmOpen(),
                      },
                      {
                        className : 'w100',
                        size : 'btn-lg',
                        color : 'btn-primary',
                        text: (tab === "clip" ? "" : "") +"",
                        onClick : "",
                      },
                    ]}
                    className="half"
                  />
                </div>
              </>
            ) : tab === "tag" ? (
              <>
                <div className="form-box">
                  <div className="form-container">
                    <div className="flex form-group">
                      <label className="col-form-label  req" htmlFor="text"></label>
                      <div className="col-form">
                        <div className="form-wrap  text">
                          <input id="text" name="text" type="text" className="form-control " placeholder="" autoComplete="off" value="" />
                        </div>
                      </div>
                    </div>

                    <div className="flex form-group">
                      <label className="col-form-label req" htmlFor="tagId"></label>
                      <div className="col-form">
                        <div className="form-wrap button">
                          <Button attr={{
                            className: "dec t-btn",
                            text:"  .",
                            onClick : handleTaglayerOpen,
                          }} />
                          <input type="hidden" id="tagId" name="tagName" />
                        </div>
                      </div>
                    </div>

                    { tagFmData1.map((item, index) => (
                      <FormItem
                        key={index}
                        attr={item}
                      />
                    ))}
                  </div>
                </div>

                <div className="btt-btn t1">
                  <BtnLayout
                    attr={[
                      {
                        className : 'w100',
                        text: '',
                        size : 'btn-lg',
                        color : 'btn-gray',
                        onClick:() => handleFmOpen("tag"),
                      },
                      {
                        className : 'w100',
                        size : 'btn-lg',
                        color : 'btn-primary',
                        text: (tab === "clip" ? "" : "") +"",
                        onClick : "",
                      },
                    ]}
                    className="half"
                  />
                </div>
              </>
            ) : ("")
          )}

          {datas && !fmVisible && (
            <ul className="card-flex flex">
              {datas.map((item, index) => (
                <li key={index} className="f1">
                  <ClipCard
                    key={index}
                    title={item.title}
                    team={item.team}
                    content={item.content}
                    stadium={item.stadium}
                    img={item.img}
                    owner="my"
                  />
                </li>
              ))}
            </ul>
          )}

          {tagList && !fmVisible && (
            <div className="mark-container">
              <div className="chk-label t-btn">
                <BubbleTab tabs={bubbleTabData} />
              </div>

              <ul className="mark-list">
                {tagList.map((item, index) => (
                  <li key={index}>
                    <div className="flex-tag">
                      <div className="col col-mark"><p className={`mark ellip ${item.mark}`}>{item.dec}</p></div>
                      <div className="col col-tag">{item.tag}</div>
                      <div className="col col-time">{item.time}</div>
                      <div className="col col-btn">
                        {item.reported ? (
                          <SelectSet
                            attr={{
                              label: "",
                              options : [
                                {
                                  type: "btn",
                                  text: "",
                                  onClick: handleUpdateOpen,
                                },
                                {
                                  type: "pop",
                                  text: "",
                                  popDatas:{
                                    popBtn: "",
                                    title: " \n?",
                                    button: {
                                      text: "",
                                      onClick: "",
                                    },
                                  },
                                },
                              ],
                              layers: {
                                selectType: "btn",
                                optionLabel: "",
                                tagHide: "",
                                optionType: "btn",
                              },
                              buttons: {
                                text: "",
                                className: "ico-etc",
                                iconOnly: true,
                                btnText: "",
                              },
                            }}
                            ref={SelectSetCompRef}
                          />
                        ) : (
                          <Button
                            attr={{
                              text: "",
                              className: "ico-alarm",
                              iconOnly: true,
                              onClick:handleMove
                            }}
                          />
                        )}
                      </div>
                    </div>
                  </li>
                ))}
              </ul>
            </div>
          )}
        </div>

        {(tab === "clip" || tab === "tag") && (
          !fmVisible && (
            <div className="btt-btn t1">
              <Button
                attr={{
                  className : 'w100',
                  text: (tab === "clip" ? "" : "") +"",
                  size : 'btn-lg',
                  color : 'btn-primary',
                  onClick:() => handleFmOpen(tab)
                }}
              />
            </div>
          )
        )}
      </div>
    </>
  )
};

export default TabContainer;
