const AreaSelect = () => {
  return (
    <div className="area-container">
      <TabsWrap attr={{sticky:false}}>
        {areaData.map((item, index) => (
          <div label={item.city} key={index}>
            <ul className="districts-flex flex">
            {item.districts.map((item2, index2) => (
              <li key={index2}>
                <Radio attr={{
                  id:"districts_"+ index2,
                  name:"districts",
                  className:"t-btn",
                  value: item2.value,
                  label: item2.text,
                  onChange: "",
                  checked: "",
                  disabled: "",
                }} />
              </li>
            ))}
            </ul>
          </div>
        ))}
      </TabsWrap>
    </div>
  );
};

