import React, { useState } from "react";
import { Link } from "react-router-dom/cjs/react-router-dom";

const PageFooter = () => {

  const [copyVisible, setCopyVisible] = useState(false);
  const  handleCopyClick = () => {
    setCopyVisible(!copyVisible);
  }
  return (
    <>
      <dl className="page-footer">
        <dt>
          <Link to="#" onClick={handleCopyClick} className={`btn-drop ${copyVisible ? "_open" : ""}`}>()  </Link>
          {copyVisible && (
            <div className="drop-wrap">
              <dl>
                <dt> </dt>
                <dd>000</dd>
              </dl>
              <dl>
                <dt></dt>
                <dd>000-00-00000</dd>
              </dl>
              <dl>
                <dt></dt>
                <dd>00000</dd>
              </dl>
              <dl>
                <dt></dt>
                <dd>    182,  2 ( 644)</dd>
              </dl>
              <dl>
                <dt></dt>
                <dd>help@hogak.co.kr</dd>
              </dl>
              <dl>
                <dt></dt>
                <dd>help@hogak.co.kr</dd>
              </dl>
              <dl>
                <dt> </dt>
                <dd> </dd>
              </dl>
            </div>
          )}
        </dt>
        <dd>
          <p className="copy">Copyright . All Rights Reserved.</p>
        </dd>
      </dl>
    </>
  );
};

export default PageFooter;
