import Badge from "components/atom/Badge";
import Button from "components/atom/button/Button";
import SelectSet from "components/molecule/form/SelectSet";
import ball from "assets/images/card/icon_ball.svg";

/**
 *
 * @param {*} props label : 
 * @param {*} props id : id 
 * @param {*} props name : input 
 * @param {*} props defaultValue : *
 * @param {*} props status :   (is-valid / is-invalid)
 * @param {*} props onChange : input   
 * @param {*} props checked : 
 *
 */

// Checkbox  , id      GroupId   ,
// name        .

const ClipCard = ({ title, team, content, stadium, img, calc, onClick, owner = '' }) => {
  return (
    <div
      className={`card_wrapper ${calc && "calc"} clip `}
      style={{ backgroundImage: `url(${img})` }}
      onClick={onClick}
    >
      <div className="card_top clip">
        <Badge attr={{ type: "clip" }} />

        {owner === "my" && (
          <ul className="col-btn">
            <li>
              <Button
                attr={{
                  text:"",
                  className : `ico-download`,
                  iconOnly: true,
                }}
              />
            </li>
            <li>
              <SelectSet
                attr={{
                  //status:status,
                  type: "button",
                  id: "SelectSetId",
                  name: "SelectSetName",
                  layers: {
                    selectType: "btn",
                    optionLabel: " ",
                    optionType: "btn",
                  },
                  buttons: {
                    text:"",
                    className : `ico-edit2`,
                    iconOnly: true,
                  },
                  options : [
                    {
                      type: "btn",
                      text: "",
                      onClick: "",
                    },
                    {
                      type: "btn",
                      text: "",
                      onClick: "",
                    },
                  ],
                }}
              />
            </li>
          </ul>
        )}
      </div>
      <div className="card_bottom clip">
        <div className="clip_title">
          {title}
          <span className="team_info">{team}</span>
        </div>
        <div className="clip_content">
          <div dangerouslySetInnerHTML={{ __html: content }} />
          <div className="btt">
            <span className="clip_stadium">{stadium}</span>
            <div className="live_icon">
              <img src={ball} alt="Ball Icon" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ClipCard;
