/**
 *
 * @param {*} props label : 
 * @param {*} props id : id 
 * @param {*} props name : input 
 * @param {*} props defaultValue : *
 * @param {*} props status :   (is-valid / is-invalid)
 * @param {*} props onChange : input   
 * @param {*} props checked : 
 *
 */

import Badge from "components/atom/Badge";
import Skeleton from "react-loading-skeleton";

// Checkbox  , id      GroupId   ,
// name        .

const ScheduleCard = ({
  progressType,
  img,
  startDate,
  endDate,
  calc,
  onClick,
}) => {
  const progressData =
    progressType === "progress"
      ? ""
      : progressType === "schedule"
      ? ""
      : progressType === "end"
      ? ""
      : "";

  const cardTopClass =
    progressType === "progress"
      ? "card_top progress"
      : progressType === "schedule"
      ? "card_top schedule"
      : progressType === "end"
      ? "card_top end"
      : "card_top progress";

  const imgBox = {
    backgroundImage: "url(" + img + ")",
  };

  const testdata = null;

  return (
    <div
      className={`card_wrapper ${calc && "calc"} schedule `}
      onClick={onClick}
    >
      {testdata ? (
        <div className={cardTopClass}>
          <div className="schedule_wrapper flexCW">
            {progressData}
            {progressType === "progress" ? <Badge attr={{ live: true }} /> : ""}
          </div>
          <div className="schedule_date">
            {startDate} ~ {endDate}
          </div>
        </div>
      ) : (
        <div className={cardTopClass}>
          <Skeleton width={60} />
        </div>
      )}

      <div className="card_bottom schedule">
        {testdata ? (
          <div className="img-box" style={imgBox}></div>
        ) : (
          <Skeleton containerClassName={"skt_img schedule"} />
        )}
      </div>
    </div>
  );
};

export default ScheduleCard;
