import "react-datetime/css/react-datetime.css";
import DateTime from "react-datetime";
import moment from "moment";
import "moment/locale/ko";

/** Date handling flow (API Response  handling  API Request)
 * 1. API Response
 *      -  date     string  format('YYYY-MM-DD')   state     .
 * 2. Handling
 *      -  date   datePicker   moment  .
 *      -  date format('YYYY-MM-DD')   state     .
 * 3. API Request
 *      -   moment   .
 *
 */

function RangePicker({ attr }) {
  const {
    style,
    closeOnSelect,
    dateFormat,
    timeFormat,
    labelRatio,
    inputRatio,
    label,
    required,
    startDateRef,
    endDateRef,
    startDate,
    endDate,
    minDateDisabled,
    minDateChange,
    minDateRange,
    maxDateRange,
    maxDateDisabled,
    maxDateChange,
  } = attr;

  return (
    <div className="range-picker form-group row m-b-10" style={style}>
      <label className={"col-form-label"}>
        {label}
        {required ? <span style={{ color: "#ff4d4f" }}> *</span> : ""}
      </label>
      <div className="range-wrapper flexSbc">
        <div className="range-container">
          <div className="date-picker">
            <DateTime
              ref={startDateRef}
              value={startDate ? moment(startDate) : null}
              isValidDate={minDateRange}
              inputProps={{
                placeholder: "",
                disabled: minDateDisabled,
                readOnly: true,
                style: { backgroundColor: minDateDisabled && "#e9ecef" },
              }}
              closeOnSelect={closeOnSelect}
              onChange={minDateChange}
              dateFormat={dateFormat || "YYYY-MM-DD"}
              timeFormat={timeFormat === false ? false : "HH:mm:ss"}
              locale="ko"
            />
          </div>
          ~
          <div className="date-picker">
            <DateTime
              ref={endDateRef}
              value={endDate ? moment(endDate) : null}
              isValidDate={maxDateRange}
              inputProps={{
                placeholder: "",
                disabled: maxDateDisabled,
                readOnly: true,
                style: { backgroundColor: maxDateDisabled && "#e9ecef" },
              }}
              closeOnSelect={closeOnSelect}
              onChange={maxDateChange}
              dateFormat={dateFormat || "YYYY-MM-DD"}
              timeFormat={timeFormat === false ? false : "HH:mm:ss"}
              locale="ko"
            />
          </div>
        </div>
      </div>
    </div>
  );
}

export default RangePicker;
