import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";

import useMediaQueryHook from "hook/useMediaQuery";

import Button from "components/atom/button/Button";
import { Link, useLocation } from "react-router-dom/cjs/react-router-dom";
import BtnLayout from "components/molecule/layout/BtnLayout";

const Hgrp03020201 = (props) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();
  const location = useLocation();
  const [data, setData] = useState(location?.state);

  const [isExpanded, setIsExpanded] = useState(false);
  const [scrollY, setScrollY] = useState(0);
  const [prevScrollY, setPrevScrollY] = useState(0); //   

  const handleScroll = () => {
    const currentScrollY = window.scrollY;
    setScrollY(currentScrollY);

    if (currentScrollY > prevScrollY && currentScrollY > 100) {
      setIsExpanded(false);
    } else if (currentScrollY < prevScrollY) {
      setIsExpanded(true);
    }

    setPrevScrollY(currentScrollY);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [prevScrollY]);

  const handleRemoveAddress = (index) => {
    setTeamData((prevData) => {
      const updatedAddresses = prevData.officeAddress.filter(
        (_, i) => i !== index
      );
      return { ...prevData, officeAddress: updatedAddresses };
    });
  };

  const [teamData, setTeamData] = useState({
    name: "  ",
    member: 30,
    video: 182,
    phone: "02-262-7899",
    officeAddress: [
      {
        address: "   32 66  201",
        detailAddress:
          "[]     182(. )",
        className: "border_bt",
      },
      {
        address: "   32 66  201",
        className: "border_bt",
      },
      {
        address: "   32 66  201",
        detailAddress:
          "[]     182(. )",
      },
    ],
    stadiumAddress: "   223",
    website: "https://aisportstv.co.kr/",
    association:
      " U-13      - U13() ,  , ,  ",
    foundation: "2011 ",
    manager: " ",
    trainingTime: "- ,,, 16:00~18:30 <br/> -  14:30~17:00 ",
    trainingSite: " /  ",
  });

  console.log(data);

  return (
    <>
      <div className="subHome_wrapper">
        {!isMobile ? (
          <div className="teamInfo_Page flexCC">{data?.label}</div>
        ) : (
          ""
        )}

        <div className="page _full">
          <section className="subHome_section address">
            <div className="address_wrapper">
              <table className="addressList">
                <tbody>
                  <tr>
                    <table className="inner">
                      {/* <thead>
                      <tr className="header">
                        <th>
                          <p>{data?.name ? data?.name : ""} </p>
                        </th>
                      </tr>
                    </thead> */}

                      <tbody>
                        {teamData.officeAddress.map((item, index) => (
                          <tr className={`item ${item.className}`} key={index}>
                            <Link
                              to={{
                                pathname: "/H-GRP/03020202",
                                state: {
                                  label: teamData.name,
                                  data: teamData.officeAddress[index],
                                },
                              }}
                            >
                              <td>
                                {item.address}
                                {item.detailAddress && (
                                  <p className="addText">
                                    {item.detailAddress}
                                  </p>
                                )}
                              </td>
                            </Link>

                            <div className="btn_wrapper">
                              <Button
                                attr={{
                                  className: `ico-gray-cancel`,
                                  iconOnly: true,
                                  onClick: () => handleRemoveAddress(index),
                                }}
                              ></Button>
                            </div>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </tr>
                </tbody>
              </table>
            </div>
          </section>
          <div className="save-button">
            <BtnLayout
              attr={[
                {
                  className: "w100",
                  text: data?.name ? data?.name + " " : "",
                  size: "btn-lg",
                  color: "btn-primary",
                },
              ]}
              className="full"
            />
          </div>
        </div>
      </div>
    </>
  );
};

export default withRouter(Hgrp03020201);
