/**
 *

 * @param {*} props label : 
 * @param {*} props id : id 
 * @param {*} props type :   default live || yet
 * @param {*} props name : input 
 * @param {*} props defaultValue : *
 * @param {*} props status :   (is-valid / is-invalid)
 * @param {*} props onChange : input   
 * @param {*} props checked : 

 *
 */

import no_img from "assets/images/card/no_member.png";

// Checkbox  , id      GroupId   ,
// name        .

const MemberCard = ({
  type,
  img,
  name,
  position,
  school,
  birthdate,
  backNumber,
  parentName,
}) => {
  return (
    <div className="member-card">
      <div className="member-photo">
        <img src={img ? img : no_img} alt="member" />
        {backNumber && <span className="back-number">{backNumber}</span>}{" "}
      </div>
      <div className="member-info">
        <div className="name-position">
          <span className="name">{name}</span>
          <span className="position">{position}</span>
        </div>
        {type === "player" ? (
          <div className="info">
            <div className="school-info">
              <span className="label"></span>
              <span className="value">{school}</span>
            </div>
            <div className="birth-info">
              <span className="label"></span>
              <span className="value">{birthdate}</span>
            </div>
          </div>
        ) : type === "parent" ? (
          <div className="parent">({parentName})</div>
        ) : (
          ""
        )}
      </div>
    </div>
  );
};

export default MemberCard;
