import React, { useState } from "react";
import { withRouter } from "react-router-dom";
import Confirm from "components/molecule/alert/Confirm";
import { toast } from "react-toastify";
import Button from "components/atom/button/Button";

const ConfirmData = [
  {
    popBtn: "",
    title: "",
    dec: (
      <>
           <br />       <br /> 
         .
      </>
    ),
    nowrap: true,
    alert: true,
    button: {
      text: "",
      onClick: "",
    },
  },

  {
    popBtn: "2",
    title: "",
    dec: (
      <>
               <br />  , 
          <br /> .  ?
      </>
    ),
    nowrap: true,
    button: [
      {
        text: "",
        color: "btn-line-white",
        onClick: "",
      },
      {
        text: "",
        color: "btn-red",
        onClick: "",
      },
    ],
  },
  {
    popBtn: "",
    title: "",
    dec: (
      <>
              <br />     
        .
      </>
    ),
    nowrap: true,
    alert: true,
    button: {
      text: "",
      onClick: "",
    },
  },
  {
    popBtn: "2",
    title: "",
    dec: (
      <>
           ,   <br />    , 
        <br />
         .  ?
      </>
    ),
    nowrap: true,
    button: [
      {
        text: "",
        color: "btn-line-white",
        onClick: "",
      },
      {
        text: "",
        color: "btn-red",
        onClick: "",
      },
    ],
  },
];

const Hteam01000000P1 = (props) => {
  const [confirmOpen, setConfirmOpen] = useState(false);

  const openConfirmHandler = (id) => {
    setConfirmOpen((el) => (el.confirmOpen !== id ? id : ""));
    if (confirmOpen === id) {
      setConfirmOpen(false);
    }
  };

  const handleShowToast = (msg) => {
    return () => toast(msg);
  };

  return (
    <>
      <section className="sec">
        <div className="m-main">
          <ul className="flex">
            {ConfirmData.map((item, index) => (
              <li style={{ marginRight: "5px" }}>
                <button
                  key={index}
                  className="btn"
                  onClick={() => {
                    openConfirmHandler(index);
                  }}
                >
                  {item.popBtn}
                </button>
                {confirmOpen === index ? (
                  <Confirm
                    data={item}
                    onClose={openConfirmHandler}
                    id={index}
                  />
                ) : (
                  ""
                )}
              </li>
            ))}
            <Button
              attr={{
                text: "  toast",
                onClick: handleShowToast("    ."),
              }}
            />
            <Button
              attr={{
                text: "  toast",
                onClick: handleShowToast(" ."),
              }}
            />
            <Button
              attr={{
                text: "  toast",
                onClick: handleShowToast(" ."),
              }}
            />
            <Button
              attr={{
                text: "  toast",
                onClick: handleShowToast(" ."),
              }}
            />
            <Button
              attr={{
                text: "  toast",
                onClick: handleShowToast(" ."),
              }}
            />
            <Button
              attr={{
                text: "  toast",
                onClick: handleShowToast(" ."),
              }}
            />
            <Button
              attr={{
                text: "   toast",
                onClick: handleShowToast("  ."),
              }}
            />
            <Button
              attr={{
                text: "   toast",
                onClick: handleShowToast("  ."),
              }}
            />
          </ul>
        </div>
      </section>
    </>
  );
};

export default withRouter(Hteam01000000P1);
