import React, {useRef, useState} from "react";
import useMediaQueryHook from "../../../hook/useMediaQuery";
import Button from "components/atom/button/Button";
import MultiContainer from "components/molecule/vod/MultiContainer";
import ClipContainer from "components/molecule/vod/ClipContainer";
import TagContainer from "components/molecule/vod/TagContainer";
import TagProgress from "components/molecule/vod/TagProgress";
import SelectSet from "components/molecule/form/SelectSet";
import ConfirmSet from "components/molecule/alert/ConfirmSet";

const VodContainer = ({vodData, vodActive, changeParentsFn}) => {
  const { currentMediaQuery, isMobile, isTablet } = useMediaQueryHook();

  const ComfirmCompRef = useRef({});

  const [isFull, setIsFull] = useState(false);
  const handleFullClick = () => {
    setIsFull(!isFull);
  };

  const [tagVisible, setTagVisible] = useState(false);

  function handleVodClick(sid) {
    changeParentsFn(sid);
  }

  // Todo connfirm save btn action
  const handleSaveClick = () => {

    ComfirmCompRef.current.openConfirmHandler();
  };

  return (
    <div className={`vod-container ${isFull ? 'full' : ''} _dim`}>
      <video controls className="video" poster={vodData.thumb}>
        <source src="" type="video/webm" />
      </video>

      {/*  */}
      <div className="right-btn-container">
        <ul className="right-btn ico-ul">
          {!vodActive.clipLayer && (
            <li>
              <Button attr={{
                className : `ico-clip`,
                text: '',
                widthTxt: true,
                onClick:() => handleVodClick('clip'),
              }} />
            </li>
          )}
          {!vodActive.clipLayer && (
            <li>
            <Button attr={{
              className : `ico-tag`,
              text: '',
              widthTxt: true,
              onClick :() => handleVodClick(),
            }} />
          </li>
          )}
          {vodActive.clipLayer && (
            <>
              <li>
                <ConfirmSet
                  btnDatas={{
                    className : `ico-save`,
                    text: '',
                    widthTxt: true,
                  }}
                  confirmDatas={{
                    popBtn : " ",
                    title: " ",
                    chkList: [
                      {
                        type:"radio",
                        name :"r1",
                        label : "",
                        checked : true,
                      },
                      {
                        type:"radio",
                        name :"r1",
                        label : "",
                      },
                      {
                        type:"radio",
                        name :"r1",
                        label : "",
                      },
                    ],
                    button: {
                      text: '',
                      onClick:handleSaveClick,
                    },
                  }}
                  ref={ComfirmCompRef}
                />
              </li>
              <li>
                <Button attr={{
                  className : `ico-cancel`,
                  text: '',
                  widthTxt: true,
                  onClick:() => handleVodClick(),
                }} />
              </li>
            </>
          )}
        </ul>
      </div>

      {/**/}
      <Button attr={{
        className : `ico-back`,
        text: vodData.title,
        widthTxt: true,
      }} />

      {/* */}
      <ul className="play-btn">
        <li>
          <Button attr={{
            className : `vod-prev`,
            text: '-10',
          }} />
        </li>
        <li>
          <Button attr={{
            className : `vod-play`,
            text: 'play',
            iconOnly: true,
          }} />
          <Button attr={{
            className : `vod-pause`,
            text: 'play',
            iconOnly: true,
          }} />
        </li>
        <li>
          <Button attr={{
            className : `vod-prev`,
            text: '+10',
          }} />
        </li>
      </ul>

      {/**/}
      <ul className="top-btn ico-ul">
        {vodActive.tagBS && (
          <li>
            <SelectSet attr={{
              label: "",
              name:"selectName",
              id:"selectId",
              options : [
                {
                  text: "",
                  value:"",
                  checked:"",
                  onChange:"",
                  rows : {
                    type : "checkbox",
                    name:"r3",
                    checked:"",
                    datas: [
                      {
                        text: "1",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "2",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "3",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "4",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "5",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "6",
                        value:"",
                        checked:"",
                      },
                    ]
                  }
                },
                {
                  text: "",
                  value:"",
                  checked:"",
                  onChange:"",
                  rows : {
                    type : "checkbox",
                    name:"r3",
                    checked:"",
                    datas: [
                      {
                        text: "1",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "2",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "3",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "4",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "5",
                        value:"",
                        checked:"",
                      },
                      {
                        text: "6",
                        value:"",
                        checked:"",
                      },
                    ]
                  }
                },
                {
                  text: "",
                  value:"",
                  checked:"",
                  onChange:"",
                },
              ],
              layers: {
                selectType : "btn",
                optionLabel : "",
                tagHide : true,
                optionType : "radio",   // default radio || checkbox
              },
              buttons : {
                className : `ico-tag`,
                text: '  VIEW  ',
                iconOnly: true,
              },
            }} />
          </li>
        )}
        {(isMobile || isTablet) && (
          <li className="airplay">
            <Button attr={{
              className : `ico-screencast`,
              text: 'Airplay',
              iconOnly: true,
            }} />
          </li>
        )}
        {!vodActive.multiLayer && (
          <li>
            <Button
              attr={{
                className : `ico-multiview`,
                text: '',
                iconOnly: true,
                onClick:() => handleVodClick('multiLayer'),
              }}
            />
          </li>
        )}
      </ul>

      {/**/}
      <ul className="btt-btn ico-ul">
        <li>
          <Button attr={{
            className : `ico-speed`,
            text: '',
            iconOnly: true,
          }} />
        </li>
        <li>
          <Button attr={{
            className : `ico-picture`,
            text: '',
            iconOnly: true,
          }} />
        </li>
        <li>
          <Button attr={{
            className : `ico-vol`,
            text: ' ',
            iconOnly: true,
          }} />
        </li>
        <li>
          <Button attr={{
            className : `ico-full`,
            text: '',
            iconOnly: true,
            onClick:() => handleFullClick()
          }} />
        </li>
      </ul>

      <div className="middle-txt">
        <Button attr={{
          className : `btn-prev`,
          text: ``,
          iconOnly: true,
        }} />
        <strong className="tit"></strong>
        <Button attr={{
          className : `btn-next`,
          text: ``,
          iconOnly: true,
        }} />
      </div>

      <div className="alert-bar">  </div>

      {vodActive.multiLayer && (
        <MultiContainer multiData={vodData.multiData} close={changeParentsFn} />
      )}

      {vodActive.clipLayer && (
        <ClipContainer clipData={vodData.clipData} />
      )}

      {vodActive.tagTime && (
        <TagProgress progressData={vodData.tagPrograss} />
      )}

      {vodActive.tagLayer && (
        <TagContainer close={changeParentsFn} />
      )}
    </div>
  );
};

export default VodContainer;
