import React, { useState, useContext, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import useMediaQueryHook from "hook/useMediaQuery";
import AppHeader from "components/organism/header/AppHeader";
import PageHeader from "components/molecule/layout/PageHeader";
import BubbleTab from "components/atom/BubbleTab";
import FormItem from "components/molecule/form/FormItem";
import FormSearch from "components/molecule/search/FormSearch";
import InfoCard from "components/molecule/Card/InfoCard";
import BtnLayout from "components/molecule/layout/BtnLayout";
import FullHeight from "components/molecule/layout/FullHeight";
import ColTop from "components/molecule/layout/ColTop";
import ColBottom from "components/molecule/layout/ColBottom";
import handleChange from "handler/handleChange";

import {infoCardData} from "components/pages/main/data";

const formGroup = [
  {
    label: "",
    required : true,
    id : "in1",
    name : "",
    type : "textarea",
    placeholder : "    .",
  },
  {
    label: "",
    id : "in1",
    name : "checkbox",
    type : "checkbox",
    options : [
      {
        type : "checkbox",
        text : " ",
        value : "",
        id : "chk1",
        name : "chk1",
        checked : "",
      },
    ],
  },
  {
    label: "",
    required : true,
    id : "in1",
    name : "select",
    type : "select",
    optionLabel: "",
    options: [
      {
        text: "",
        value: "",
        name: "s1",
      },
      {
        text: "",
        value: "",
        name: "s1",
      },
    ],
    placeholder : " .",
  }
]
const Hmain07030000 = (props) => {
  return (
    <>
      <PageHeader onClick="" title="" />
      <div className="page page-wrapper page-shoot">
        <FullHeight attr={{pageHeader:true}}>
          <ColTop>
            <div className="location-container">
              <header className="location-header">
                <strong className="stadium">   A</strong>
                <p className="date">2024 10 24 12:00~14:55</p>
              </header>
              <p className="tip">*      5(55) .</p>
            </div>
            <div className="info-container">
              <div className="label-game">
                <strong className="game baseball"></strong>
              </div>

              <div className="shoot-container">
                <dl className="shoot-dl">
                  <dt><span className="req"></span></dt>
                  <dd>
                    <div className="form-box">
                      <BubbleTab tabs={[{ label: "" }, { label: "" }, { label: "" }]}/>
                    </div>
                  </dd>
                </dl>

                <div className="shoot-dl form-group">
                  <FormSearch attr={{
                    name: "",
                    placeholder: " .",
                    onClick: "",
                  }}
                  />
                </div>

                <dl className="shoot-dl">
                  <dt><span className="req"></span></dt>
                  <dd>
                    {infoCardData.map((item, index) => (
                      <InfoCard
                        type={item.type}
                        team1={item.team1}
                        team2={item.team2}
                      />
                    ))}
                  </dd>
                </dl>
              </div>

              <div className="form-container _wrap">
                { formGroup
                  .map((item, index) => (
                    <FormItem
                      key={index}
                      attr={item}
                    />
                  ))
                }
              </div>
            </div>
          </ColTop>
          <ColBottom>
            <div className="agree-container">
                <BtnLayout
                  attr={[
                    {
                      className : 'w100',
                      text: ' (2/3)',
                      disabled: true,
                      color:"btn-black",
                      size:"btn-lg",
                      onClick:"",
                    },
                  ]}
                  className="full"
                />
              </div>
          </ColBottom>
        </FullHeight>
      </div>
    </>
  );
};

export default withRouter(Hmain07030000);
